/* Sticky footer styles
-------------------------------------------------- */
html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 60px; /* Margin bottom by footer height */
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height: 60px; /* Vertically center the text there */
  background-color: #f5f5f5;
}

.selected-sip {
  box-shadow: 0px 0px 0px 2px rgb(220, 53, 69);
}

.login-spinner {
  font-size: 24px;
}

.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}

.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}

.form-signin .form-control:focus {
  z-index: 2;
}

.form-signin input[type='email'] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type='password'] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.sip-peer-body.card-body {
  padding: 5px 10px;
}

.channel-ringing {
  will-change: transform;
  -webkit-animation: shake 0.1s ease-in-out 0.1s infinite alternate;
          animation: shake 0.1s ease-in-out 0.1s infinite alternate;
}

@-webkit-keyframes shake {
  from {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg);
  }
  to {
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
}

.card-header.sip-peer-header {
  padding: 5px 10px;
}

.sip-peer-card-footer.sip-peer-card-footer {
  padding: 5px 10px;
}

